.wrapper {
  width: 6.25rem;
  height: 6.25rem;
  background-color: rgb(var(--cl-dark));
}
.circle {
  composes: wrapper;
  overflow: hidden;
  border-radius: 50%;
  box-shadow: var(--shadow-md);
}
.avatar {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
